export const connectWallet = async () => {
  if (window.ethereum) {
    try {
      const addressArray = await window.ethereum.request({
        method: "eth_requestAccounts",
      });
      const obj = {
        status: "üëÜüèΩ Click above button to play the game.",
        success: "true",
        address: addressArray[0],
      };
      return obj;
    } catch (err) {
      return {
        address: "",
        success: "false",
        status: "üò• " + err.message,
      };
    }
  } else {
    return {
      address: "",
      success: "false",
      status: (
        <span>
          <p>
            {" "}
            ü¶ä{" "}
            <a target="_blank" href={`https://metamask.io/download.html`} rel="noreferrer">
              You must install Metamask, a virtual Ethereum wallet, in your
              browser.
            </a>
          </p>
        </span>
      ),
    };
  }
};

export const getCurrentWalletConnected = async () => {
  if (window.ethereum) {
    try {
      const addressArray = await window.ethereum.request({
        method: "eth_accounts",
      });
      if (addressArray.length > 0) {
        return {
          address: addressArray[0],
          status: "üëÜüèΩ Click the above button to paly the game.",
        };
      } else {
        return {
          address: "",
          status: "ü¶ä Connect to Metamask using the above button.",
        };
      }
    } catch (err) {
      return {
        address: "",
        status: "üò• " + err.message,
      };
    }
  } else {
    return {
      address: "",
      status: (
        <span>
          <p>
            {" "}
            ü¶ä{" "}
            <a target="_blank" href={`https://metamask.io/download.html`} rel="noreferrer">
              You must install Metamask, a virtual Ethereum wallet, in your
              browser.
            </a>
          </p>
        </span>
      ),
    };
  }
};